.youtube {
  display: flex;
  flex-direction: column;
  border-radius: 8px;
  .BaseEmbeddedLink_baseEmbeddedLinkContainer__SlSm5 {
    --container-padding: 1.5rem;
    background-color: var(--black);
    border-radius: var(--link-border-radius);
    display: flex;
    flex-direction: column;
    transition: all 0.5s cubic-bezier(0.74, -0.18, 0.3, 0.48),
      background-color 1.5s ease-in-out;
    padding: var(--container-padding);
    position: relative;
    max-width: unset;
    margin: 0;
    width: 100%;
    padding-top: calc(56.7% - var(--container-padding));
    z-index: 0;
  }
  .EmbeddedYouTube_embeddedVideoIframe__BnmH8,
  .EmbeddedYouTube_embeddedVideoThumbnail__X8kI_,
  .EmbeddedYouTube_featuredLinkEmbeddedVideo__GERzu {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    border: 0.6px solid #9c9c9c;
    border-radius: 8px;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
  }
  .reactYoutube {
    iframe {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
    }
  }

  .swiper-content {
    border-top: 0.6px solid #9c9c9c;
    border: none !important;
    h4 {
      user-select: none;
    }
  }
}

.theme-etyne {
  .youtube {
    .EmbeddedYouTube_embeddedVideoIframe__BnmH8,
    .EmbeddedYouTube_embeddedVideoThumbnail__X8kI_,
    .EmbeddedYouTube_featuredLinkEmbeddedVideo__GERzu {
      border-radius: unset;
      border: none;
    }

    .swiper-content {
      p {
        font-family: "Quicksand";
        font-weight: 700;
        font-size: 18px;
        letter-spacing: 0.228px;
        color: rgba(0, 32, 76, 0.9);
      }
    }
  }
}
.theme-phiverse {
  .youtube {
    border-radius: 20px;
    .EmbeddedYouTube_embeddedVideoIframe__BnmH8,
    .EmbeddedYouTube_embeddedVideoThumbnail__X8kI_,
    .EmbeddedYouTube_featuredLinkEmbeddedVideo__GERzu {
      border-radius: unset;
    }
  }
}

.BaseEmbeddedLink_baseEmbeddedLinkContainer__SlSm5 {
  --container-padding: 1.5rem;
  background-color: var(--black);
  border-radius: var(--link-border-radius);
  display: flex;
  flex-direction: column;
  transition: all 0.5s cubic-bezier(0.74, -0.18, 0.3, 0.48),
    background-color 1.5s ease-in-out;
  padding: var(--container-padding);
  position: relative;
  max-width: unset;
  margin: 0;
  width: 100%;
  padding-top: calc(56.7% - var(--container-padding));
  z-index: 0;
}
.EmbeddedYouTube_embeddedVideoIframe__BnmH8,
.EmbeddedYouTube_embeddedVideoThumbnail__X8kI_,
.EmbeddedYouTube_featuredLinkEmbeddedVideo__GERzu {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.videoDirect {
  ._username__featuredLink__MeYB7 {
    padding: 8px;
    background: #fff;
    border-radius: 24px;
    .EmbeddedYouTube_embeddedYouTube__kZebg {
      border: none;
      iframe {
        border-radius: 24px;
        border: none;
      }
    }
  }
  .swiper-content {
    background: transparent !important;
    margin-top: 5px;
    p {
      font-family: "Quicksand";
      font-weight: 700;
      font-size: 18px;
      letter-spacing: 0.228px;
      color: rgba(0, 32, 76, 0.9);
    }
  }
}
.videoDirect.youtube {
  box-shadow: none;
}
.theme-consmy,
.theme-buwayne {
  .videoDirect {
    .swiper-content {
      p {
        color: $white-color;
      }
    }
  }
}
.theme-etyne {
  .videoDirect {
    ._username__featuredLink__MeYB7 {
      padding: 0px;
      background: #fff;
      border: 1px solid #0e334c;
      box-shadow: 2px 3px 0px 2px #0e334c;
      border-radius: 4px !important;

      .EmbeddedYouTube_embeddedYouTube__kZebg {
        iframe {
          border-radius: 0px;
        }
      }
    }
    .swiper-content {
      background: transparent !important;
      margin-top: 5px;
      p {
        font-family: "Quicksand";
        font-weight: 700;
        font-size: 18px;
        letter-spacing: 0.228px;
        color: rgba(0, 32, 76, 0.9);
      }
    }
  }
  .videoDirect.youtube {
    background: transparent;
    border: none;
    border-radius: 4px;
  }
}
