.theme-riorpad,
.theme-buwayne {
  .youtube {
    .neumorphic {
      box-shadow: none;
    }
    .BaseEmbeddedLink_baseEmbeddedLinkContainer__SlSm5 {
      padding-top: calc(56.6% - var(--container-padding));
    }
    border-radius: 20px;
    .EmbeddedYouTube_embeddedVideoIframe__BnmH8,
    .EmbeddedYouTube_embeddedVideoThumbnail__X8kI_,
    .EmbeddedYouTube_featuredLinkEmbeddedVideo__GERzu {
      border-radius: 25px;
      border: none;
    }
  }
  .videoDirect {
    ._username__featuredLink__MeYB7 {
      padding: 0px;
      background: transparent;
      border-radius: 0px;
    }
  }
}
.theme-essence {
  .youtube {
    .EmbeddedYouTube_embeddedVideoIframe__BnmH8,
    .EmbeddedYouTube_embeddedVideoThumbnail__X8kI_,
    .EmbeddedYouTube_featuredLinkEmbeddedVideo__GERzu {
      border: none;
    }
    .react-player__preview {
      border-radius: 6px;
      box-shadow: 1px 1px 10px #999999;
    }
  }
  .videoDirect {
    .react-player__preview {
      border-radius: 16px;
    }
  }
}
